  }

  addcancel(){

      this.withchangeModel = false
  this.addshow= false
  }

    addaddress() {

    if(this.provinceid==""){
      Toast("请选择省份");
      return;
    }
    if(this.cityid==""){
      Toast("请选择城市");
      return;      
    }
    if(this.countryid==""){
      Toast("请选择市区");
      return;
    }
    if(this.address1==""){
      Toast("请填写详细地址");
      return;
    }
    if(this.contactName==""){
      Toast("请填写收货人");
      return;
    }
    if(this.contactMobile==""){
      Toast("请手机号码");
      return;
    }
    if(this.updateaddressid){
      Vue.prototype.$reqFormPost(
      "/address/update",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token,
        addressId:this.updateaddressid,
        provinceId:this.provinceid,
        cityId:this.cityid,
        countryId:this.countryid,
        address:this.address1,
        contactName:this.contactName,
        contactMobile:this.contactMobile,
        isDefault:this.isDefault
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
          Toast(res.data.message);
          return;
        }
        Toast("新建地址成功");
        this.getAddressList();
        this.provinceid="";
        this.cityid="";
        this.countryid="";
        this.address1="";
        this.contactName="";
        this.contactMobile="";
        this.isDefault=0;
        this.addshow=false;
        this.updateaddressid="";
      }
    );
    }else{
      Vue.prototype.$reqFormPost(
      "/address/add",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token,
        provinceId:this.provinceid,
        cityId:this.cityid,
        countryId:this.countryid,
        address:this.address1,
        contactName:this.contactName,
        contactMobile:this.contactMobile,
        isDefault:this.isDefault
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
          Toast(res.data.message);
          return;
        }
        Toast("新建地址成功");
        this.getAddressList();
        this.provinceid="";
        this.cityid="";
        this.countryid="";
        this.address1="";
        this.contactName="";
        this.contactMobile="";
        this.isDefault=0;
        this.addshow=false;
        this.updateaddressid="";
      }
    );
    }
    
  }



changeprovince(){
    this.cityid="";
    this.city=[];
    this.countryid="";
    this.country=[];
    this.querycity();
  }
  changecity(){
    this.countryid="";
    this.country=[];
    this.querycountry();
  }

  setDefaultAddress(addressId){
    if(addressId==this.address[
      'addressId'
    ]){
      return
    }

    



   Vue.prototype.$reqFormPost(
      "/prepare/order/update",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token,
          prepareId:this.$store.getters[Vue.prototype.MutationTreeType.PREPAREID],
        addressId: addressId
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
          Toast(res.data.message);
          return;
        }
        
 this.getPreInfo(
      this.$store.getters[Vue.prototype.MutationTreeType.PREPAREID]
    );
 this.withchangeModel = false
  this.addshow= false
      }
    );



  }

  querycity(){
    Vue.prototype.$reqFormPost(
      "/address/querycity",
      {
        provinceId:this.provinceid
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
          Toast(res.data.message);
          return;
        }
        console.log(res.data.data.region)
        this.city=res.data.data.region
      }
    );
  }

   getAddressList() {
    Vue.prototype.$reqFormPost(
      "/address/list",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
          Toast(res.data.message);
          return;
        }
        console.log("地址列表", res.data.data.addressList);
        this.addressList = res.data.data.addressList;
        this.addressList.forEach((item, index) => {
          item["id"] = item.addressId;
          item["name"] = item.contactname;
          item["tel"] = item.contactmobile;
          item["readdress"] = item.address;
          item["address"] =
            item.province + item.city + item.country + item.address;
        });
      }
    );
  }
  querycountry(){
    Vue.prototype.$reqFormPost(
      "/address/querycountry",
      {
        cityId:this.cityid
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
          Toast(res.data.message);
          return;
        }
        console.log(res.data.data.region)
        this.country=res.data.data.region
      }
    );
  }



  withchangeModel=false;

addressList = [];
  // 删除地址信息
  deladdress="";
  deladdressid=""
  delshow=false;
  // 新增地址
  addshow=false;
  province=[];
  provinceid="";
  city=[];
  cityid="";
  country=[];
  countryid="";

  updateaddressid='';
  address1:any='';
  contactName="";
  contactMobile="";
  isDefault=0;





changeModel(type){
if(type=='change'){
  console.log('切换地址')
  this.withchangeModel = true;
this.getAddressList()
}else{

    this.getprovince();
      this.addshow=false;
    this.provinceid="";
    this.cityid="";
    this.countryid="";
    this.address1="";
    this.contactName="";
    this.contactMobile="";
    this.isDefault=0;    
    this.updateaddressid="";
  this.addshow= true;

}
 
 
}





  prepareId = "";
  shopCartList = [];
  address = {};
  totalPrice = "";
  goodsSum = "";
  freight = "";
  getPreInfo(prepareId) {
    Vue.prototype.$reqFormPost1(
      "/prepare/order/query",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token,
        prepareId: prepareId
      },
      res => {
        if (res.returnCode !== 200) {
          this["$Message"].warning(res.message);
          console.log("网络请求错误！");
          return;
        }

        this.shopCartList = res.data.shopCartList;
        this.address = res.data.address;
        console.log("address", this.address);
        this.totalPrice = res.data.totalPrice;
        this.goodsSum = res.data.goodsSum;
        this.freight = res.data.freight;
      }
    );
  }
  pageType = "";
  titlevalue = "";
  couponId = "";
  titleType = "";
  invoiceTitle = "";
  invoiceNo = "";
  remark = "";
  onSubmit() {
    if (!this.address) {
      this["$Message"].warning("请选择一个收货地址");
      return;
    }
    Vue.prototype.$reqFormPost(
      "/prepare/order/submit",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token,
        couponId: this.couponId,
        titleType: this.titleType,
        invoiceTitle: this.invoiceTitle,
        invoiceNo: this.invoiceNo,
        prepareId: this.prepareId,
        remark: this.remark
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
          Toast(res.data.message);
          return;
        }

        this.$router.replace({
          name: "pay",
          query: {
            contactname: this.address["contactname"],
            contactmobile: this.address["contactmobile"],
            address:
              this.address["province"] +
              this.address["city"] +
              this.address["country"] +
              this.address["address"],
            body: res.data.data.body,
            payId: res.data.data.payId,
            payTotal: res.data.data.payTotal
          }
        });
        console.log(res.data.data);
      }
    );
  }
   getprovince(){
    Vue.prototype.$reqFormPost(
      "/address/queryprovince",
      {},
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
          Toast(res.data.message);
          return;
        }
        console.log(res.data.data.region)
        this.province=res.data.data.region
      }
    );
  }
  mounted() {


    this.prepareId = this.$store.getters[
      Vue.prototype.MutationTreeType.PREPAREID
    ];
 

    this.getPreInfo(
      this.$store.getters[Vue.prototype.MutationTreeType.PREPAREID]
    );


  }
}



